import { NgModule } from '@angular/core';
import { Routes, RouterModule,Router } from '@angular/router';

import { ProductComponent } from './product/product.component';
import { CategoryComponent } from './category/category.component';
import { SearchComponent } from './search/search.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { CheckoutComponent } from './checkout/checkout.component';
import { ThankyouComponent } from './thankyou/thankyou.component';
import { OrdersComponent } from './orders/orders.component';
import { MyordersComponent } from './myorders/myorders.component';
import { PaymentsComponent } from './payments/payments.component';
import { EnquiryComponent } from './enquiry/enquiry.component';
import { ResetComponent } from './reset/reset.component';
import { ChangepwComponent } from './changepw/changepw.component';
import { PrivacyComponent } from './privacy/privacy.component';
import { TermsComponent } from './terms/terms.component';
import { RefundsComponent } from './refunds/refunds.component';
import { AboutComponent } from './about/about.component';
import { CategorysearchComponent } from './categorysearch/categorysearch.component';
import { MobileproductComponent } from './product/mobileproduct/mobileproduct.component';
import { ApplicationStateService } from './application-state.service';
import { MobilecategorysearchComponent } from './categorysearch/mobilecategorysearch/mobilecategorysearch.component';
import { MobilecategoryComponent } from './category/mobilecategory/mobilecategory.component';
import { MobileaboutComponent } from './about/mobileabout/mobileabout.component';
import { BlogComponent } from './blog/blog.component';
import { ChannelsComponent } from './channels/channels.component';
import { KrishiComponent } from './krishi/krishi.component';
import { YtplayerComponent } from './ytplayer/ytplayer.component';
import { AdminComponent } from './admin/admin.component';

const routes: Routes = [
	{ path: '', component: ProductComponent },
	{ path: 'admin', component: AdminComponent },
	{ path: 'category', component: CategoryComponent },
	{ path: 'search/:keyword', component: SearchComponent },
    { path: 'categorysearch/:keyword', component: CategorysearchComponent },
	{ path: 'login', component: LoginComponent },
	{ path: 'register', component: RegisterComponent },
	{ path: 'checkout', component: CheckoutComponent },
	{ path: 'order', component: OrdersComponent },
	{ path: 'myorder', component: MyordersComponent },
	{ path: 'thankyou', component: ThankyouComponent },
	{ path: 'payment', component: PaymentsComponent },
	{ path: 'enquiry', component: EnquiryComponent },
	{ path: 'reset', component: ResetComponent },
	{ path: 'pwchange', component: ChangepwComponent },
	{ path: 'privacy', component: PrivacyComponent },
	{ path: 'refunds', component: RefundsComponent },
	{ path: 'terms', component: TermsComponent },
	{ path: 'about', component: AboutComponent },
	{ path: 'blog', component: BlogComponent },
	{ path: 'channel', component: ChannelsComponent },
	{ path: 'krishi', component: KrishiComponent },
	{ path: 'tube/:videoid', component: YtplayerComponent },
];


const mobile_routes: Routes = [
	{ path: '', component: MobileproductComponent },
	{ path: 'admin', component: AdminComponent },
	{ path: 'category', component: MobilecategoryComponent },
	{ path: 'search/:keyword', component: SearchComponent },
    { path: 'categorysearch/:keyword', component: MobilecategorysearchComponent },
	{ path: 'login', component: LoginComponent },
	{ path: 'register', component: RegisterComponent },
	{ path: 'checkout', component: CheckoutComponent },
	{ path: 'order', component: OrdersComponent },
	{ path: 'myorder', component: MyordersComponent },
	{ path: 'thankyou', component: ThankyouComponent },
	{ path: 'payment', component: PaymentsComponent },
	{ path: 'enquiry', component: EnquiryComponent },
	{ path: 'reset', component: ResetComponent },
	{ path: 'pwchange', component: ChangepwComponent },
	{ path: 'privacy', component: PrivacyComponent },
	{ path: 'refunds', component: RefundsComponent },
	{ path: 'terms', component: TermsComponent },
	{ path: 'about', component: MobileaboutComponent },
	{ path: 'blog', component: BlogComponent },
	{ path: 'channel', component: ChannelsComponent },
	{ path: 'krishi', component: KrishiComponent },
	{ path: 'tube/:videoid', component: YtplayerComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {scrollPositionRestoration: 'enabled'})],
  exports: [RouterModule]
})


export class AppRoutingModule { 
	public constructor(private router: Router,
    private applicationStateService: ApplicationStateService) {

    if (applicationStateService.getIsMobileResolution()) {
      router.resetConfig(mobile_routes);
    }
}
}
