import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';

@Component({
  selector: 'app-ytplayer',
  templateUrl: './ytplayer.component.html',
  styleUrls: ['./ytplayer.component.css']
})
export class YtplayerComponent implements OnInit {
  videoid:any;	
  videourl:any;
  safeSrc: SafeResourceUrl;

  constructor(
  		private redirect:Router,
        private route:ActivatedRoute,
        private sanitizer:DomSanitizer
  ) { 
  	 this.sanitizer = sanitizer; 

  }

  ngOnInit() {
  	this.route.params.subscribe(params => {
  			this.videoid = params['videoid'];
  			console.log(this.videoid);
  			this.videourl="https://www.youtube.com/embed/"+this.videoid;
  			console.log(this.videourl);
  			this.safeSrc =  this.sanitizer.bypassSecurityTrustResourceUrl(this.videourl);
  		});
  }



}
