import { Component, OnInit } from '@angular/core';
import { YoutubeService } from '../youtube.service';
import { map, tap, takeUntil} from 'rxjs/operators';
import { DataService } from '../data.service';
import { SharedService } from '../services/shared.service';
import { Router, ActivatedRoute } from '@angular/router';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
@Component({
  selector: 'app-blog',
  templateUrl: './blog.component.html',
  styleUrls: ['./blog.component.css']
})
export class BlogComponent implements OnInit {
  videourl:any;
  safeSrc: SafeResourceUrl;	
  videos:Array<any>;
  ytBlogApi:Object;
  ytBlogList:Array<any>;
  constructor(private youTubeService: YoutubeService,private data: DataService, 
		private shared: SharedService,private route:ActivatedRoute,
        	private sanitizer:DomSanitizer) { }

  ngOnInit() {
	   
		

		this.data.napuorYTBlogs().subscribe( data => {
			this.ytBlogApi = data;
			this.ytBlogList = data['results'];
			console.log(this.ytBlogList);
			let ytb:any;
			this.videos = [];
			for (let i = 0; i < this.ytBlogList.length; i++  ){
				ytb= this.ytBlogList[i];
				this.videos.push(ytb)
			};

			}, error =>{
			if (error.status === 401) {
				this.data.sessionLogout();
			}
			console.log(error);
  		});

        /** 
		this.youTubeService
		.getVideosForChanel('UCe0Ha5QljsCV5UqIkobBrcQ', 15)
		.subscribe(lista => {
		for (let element of lista["items"]) {
		this.videos.push(element)
		}
		});
		*/
	}


	ytsafeSrc(vid:String):SafeResourceUrl{
		let videourl:any;
		let safeSrc: SafeResourceUrl;

		videourl="https://www.youtube.com/embed/"+vid;
		safeSrc =  this.sanitizer.bypassSecurityTrustResourceUrl(videourl);
		return safeSrc;



	}


  }


