import { Injectable } from '@angular/core';
import { Router } from '@angular/router';

import {Observable, of, BehaviorSubject,Subject} from 'rxjs';

import { DataService } from '../data.service';

@Injectable({
  	providedIn: 'root'
})

export class SharedService {



  //Order Status
  private orderstatus:any = String();
  private OrderStatus = new BehaviorSubject<string>(this.orderstatus); 
  orderstatus$ = this.OrderStatus.asObservable();



  // User Token
	private user_token:any = this.getToken();
	private Token = new BehaviorSubject(this.user_token);
	userToken$ = this.Token.asObservable();


	// Cart Count
	private cart_count:number = this.getCartCount();
	private Count = new BehaviorSubject(this.cart_count);
	cartCount$ = this.Count.asObservable();

  	constructor(private redirect:Router, private data: DataService) { }

   
    publishOrderStatus(data: string) {
      this.OrderStatus.next(data);
    }


  	setToken(token){
  		if(token !== false){
  			localStorage.setItem('token', token);
  		}
  		this.Token.next(token);
  	}

  	getToken(){
  		if ('token' in localStorage) {
  			return localStorage.getItem('token');
  		}
  		return false;
  	}

    setCartArray(cart){
        localStorage.setItem('cart', JSON.stringify(cart));
    }

    getCartArray(){
        let cart:any = '';
        if ('cart' in localStorage) {
            return cart = JSON.parse(localStorage.getItem('cart'));
        }
        return cart;
    }  	
	
	setCartCount(count:number){
  		this.Count.next(count);
  	}

  	getCartCount(){
        let cart; 
  		let cartCount = 0; 
  		if ('cart' in localStorage) {
  			cart = JSON.parse(localStorage.getItem('cart'));
            cartCount = cart.count;          
        }
  		return cartCount;
  	}

  	sessionLogout(){
  		localStorage.clear();
        this.setToken(false);
  		this.setCartCount(0);
        this.redirect.navigate(['']);
  	}

}
