import { Component, OnInit } from '@angular/core';
import { YoutubeService } from '../youtube.service';
import { map, tap, takeUntil} from 'rxjs/operators';
import { DataService } from '../data.service';
import { SharedService } from '../services/shared.service';

@Component({
  selector: 'app-krishi',
  templateUrl: './krishi.component.html',
  styleUrls: ['./krishi.component.css']
})
export class KrishiComponent implements OnInit {

  articles:Array<any>;
  napuorArticleApi:Object;
  napuorArticleList:Array<any>;
  constructor(private data: DataService, 
		private shared: SharedService) { }

  ngOnInit() {
	  
		

		this.data.napuorArticles().subscribe( data => {
			this.napuorArticleApi = data;
			this.napuorArticleList = data['results'];
			let art:any;
			this.articles = [];
			for (let i = 0; i < this.napuorArticleList.length; i++  ){
				art= this.napuorArticleList[i];
				
				
				this.articles.push(art)
			};
			}, error =>{
			if (error.status === 401) {
				this.data.sessionLogout();
			}
			console.log(error);
  		});

        /** 
		this.youTubeService
		.getVideosForChanel('UCe0Ha5QljsCV5UqIkobBrcQ', 15)
		.subscribe(lista => {
		for (let element of lista["items"]) {
		this.videos.push(element)
		}
		});
		*/
	}

}
