import { Component, OnInit, ViewChild } from '@angular/core';
import {Router} from "@angular/router"
import { AlertService } from 'ngx-alerts';
import { HeaderBannerComponent } from '../../header-banner/header-banner.component';
import { DataService } from '../../data.service';
import { SharedService } from '../../services/shared.service';
@Component({
  selector: 'app-mobilecategory',
  templateUrl: './mobilecategory.component.html',
  styleUrls: ['./mobilecategory.component.css']
})
export class MobilecategoryComponent implements OnInit {

  categoryApi:Object;
  categories:Array<any>;
  constructor(
  		private redirect: Router,
		private alertService: AlertService,
        private data: DataService, 
		private shared: SharedService
  ) { }

  ngOnInit() {
  		this.data.getCategories().subscribe( data => {
			this.categoryApi = data;
			this.categories = data['results'];
		}, error =>{
			if (error.status === 401) {
				this.data.sessionLogout();
			}
			console.log(error);
  		});

  }



  public categoryProducts(categoryname){
    this.redirect.navigate(['/categorysearch',categoryname]);	
  }
}
