import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { AlertService } from 'ngx-alerts';

import { DataService } from '../data.service';
import { SharedService } from '../services/shared.service';

@Component({
	selector: 'app-header',
  	templateUrl: './header.component.html',
  	styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

	public userToken:any;
	public currentUrl:string;

	public cartCount:number;
	public cartOverview:any;
	public cartShow:boolean = false;
	
	public search_keyword:any;
	public searchedProduct:boolean = false;
	public searchData:any;
	public popupProduct:any = '';
	public variation_set:any;
	public cart:any = '';
	public cartLoading = false;

  	constructor(
  		private route: ActivatedRoute,
  		private redirect: Router,
  		private alertService: AlertService,
        private shared: SharedService, 
        private data: DataService
    ) { }

  	ngOnInit() {
  		
  		this.shared.userToken$
  		.subscribe(
  			token => {
  				this.userToken = token;
  			}
  		);

  		this.shared.cartCount$
  		.subscribe(
  			count => {
  				this.cartCount = count;
  				this.getCartArray();
  				this.currentUrl = this.redirect.url;
  			}
  		)
  	}

  	searchProduct(){
  		let keyword = this.search_keyword;
	  	if(keyword !== ''){
	  		this.redirect.navigate(['search', keyword]);		
	  	}
	}

  	cartToggle() {
    	this.cartShow = !this.cartShow;		
	}

    cartCheckout() {

        this.cartShow = !this.cartShow;	
    	this.redirect.navigate(['/checkout']);
	}

	getCartArray(){
		this.cartOverview = this.shared.getCartArray();
	}

	cartIncrease = function(item){
	    let cart = this.cartOverview;
		let cartObj = {
			"cart_id": cart.cart,
			"product_id": item.product,
			"quantity": 1
		}
		this.data.updateCart(cartObj).subscribe(data =>{
			this.cart = data;	
			this.shared.setCartArray(this.cart);	
			this.shared.setCartCount(this.cart.count);	
			this.getCartArray();
		}, error =>{
			if(error.status === 401){
	            this.shared.sessionLogout();
	        }
	        console.log(error);
		});
	}
	cartBasketIncrease = function(item){
	    let cart = this.cartOverview;
		let cartObj = {
			"cart_id": cart.cart,
			"basket_id": item.basket,
			"quantity": 1
		}
		this.data.updateBasketCart(cartObj).subscribe(data =>{
			this.cart = data;	
			this.shared.setCartArray(this.cart);	
			this.shared.setCartCount(this.cart.count);	
			this.getCartArray();
		}, error =>{
			if(error.status === 401){
	            this.shared.sessionLogout();
	        }
	        console.log(error);
		});
	}

	cartDecrease = function(item){
	    let cart = this.cartOverview;
		let cartObj = {
			"cart_id": cart.cart,
			"product_id": item.product,
			"quantity": -1
		}

		this.data.updateCart(cartObj).subscribe(data =>{
			this.cart = data;	
			this.shared.setCartArray(this.cart);	
			this.shared.setCartCount(this.cart.count);	
			this.getCartArray();
		}, error =>{
			if(error.status === 401){
	            this.shared.sessionLogout();
	        }
	        console.log(error);
		});
	}

	cartBasketDecrease = function(item){
	    let cart = this.cartOverview;
		let cartObj = {
			"cart_id": cart.cart,
			"basket_id": item.basket,
			"quantity": -1
		}

		this.data.updateBasketCart(cartObj).subscribe(data =>{
			this.cart = data;	
			this.shared.setCartArray(this.cart);	
			this.shared.setCartCount(this.cart.count);	
			this.getCartArray();
		}, error =>{
			if(error.status === 401){
	            this.shared.sessionLogout();
	        }
	        console.log(error);
		});
	}


	findIndexById = function(item) {
	    let index = this.cartOverview.indexOf(item);
	    return index;
	};

	removeItem = function(item) {
	    var index = this.findIndexById(item);
	    this.cartOverview.splice(index, 1);
		this.data.setCartCount(this.cartOverview.length);
		this.data.setCartArray(this.cartOverview);
	};

	removeAll = function() {
	    this.data.setCartCount(0);
		this.data.removeCartArray();
		this.cartToggle();
	};

	logout(){
		let logoutResult:any = '';
		this.data.sessionLogout().subscribe(data =>{
			logoutResult = data;	
			this.shared.sessionLogout();
		}, error =>{
			console.log(error);
		});
	}

}
