import { Component, OnInit } from '@angular/core';

import { DataService } from '../../data.service';

@Component({
  selector: 'app-mobileheader-banner',
  templateUrl: './mobileheader-banner.component.html',
  styleUrls: ['./mobileheader-banner.component.css']
})
export class MobileheaderBannerComponent implements OnInit {

    banners:any;
	bannerResult:Array<any>;

  	constructor(private data: DataService) { }

  	ngOnInit() {
  		this.data.homeBanner().subscribe( data => {
			this.banners = data;
			this.bannerResult = this.banners.results;
		}, error =>{
			if (error.status === 401) {
				this.data.sessionLogout();
			}
			console.log(error);
		})	
  	}

}
