import { Component, OnInit } from '@angular/core';
import { YoutubeService } from '../youtube.service';
import { map, tap, takeUntil} from 'rxjs/operators';
import { DataService } from '../data.service';
import { SharedService } from '../services/shared.service';
import { Router, ActivatedRoute } from '@angular/router';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';

@Component({
  selector: 'app-channels',
  templateUrl: './channels.component.html',
  styleUrls: ['./channels.component.css']
})
export class ChannelsComponent implements OnInit {
  public videoid:any;
  videourl:any;
  safeSrc: SafeResourceUrl;	

  videos:Array<any>;
  ytChannelApi:Object;
  ytChannelList:Array<any>;
  constructor(private youTubeService: YoutubeService,private data: DataService, 
		  	private redirect: Router,private shared: SharedService,private route:ActivatedRoute,
        	private sanitizer:DomSanitizer) {

        	}

  ngOnInit() {
	  
		

		this.data.napuorYTChannels().subscribe( data => {
			this.ytChannelApi = data;
			this.ytChannelList = data['results'];
			let ytc:any;
			this.videos = [];
			for (let i = 0; i < this.ytChannelList.length; i++  ){
				ytc= this.ytChannelList[i];
				
				ytc.imgsrc="https://img.youtube.com/vi/"+ytc.videoid+"/hqdefault.jpg";
				
				this.videos.push(ytc)
			};
			}, error =>{
			if (error.status === 401) {
				this.data.sessionLogout();
			}
			console.log(error);
  		});

        /** 
		this.youTubeService
		.getVideosForChanel('UCe0Ha5QljsCV5UqIkobBrcQ', 15)
		.subscribe(lista => {
		for (let element of lista["items"]) {
		this.videos.push(element)
		}
		});
		*/
	}

	playVideo(vid:string){
	    console.log(vid);
	  	if(vid !== ''){
	  		this.redirect.navigate(['tube', vid]);		
	  	}
	}

	ytsafeSrc(vid:String):SafeResourceUrl{
		let videourl:any;
		let safeSrc: SafeResourceUrl;

		videourl="https://www.youtube.com/embed/"+vid;
		safeSrc =  this.sanitizer.bypassSecurityTrustResourceUrl(videourl);
		return safeSrc;



	}

}
